@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap);
:root {
  --white: #eaeaea;
  --black: #050505;
  --sobre: #ee258d;
  --habilidades: #fbe31a;
  --trabalhos: #03aedb;
  --contato: #6f37b7;
  --app-details: #8f9690;
  --main-color: #050505;
  --highlight-color: #050505;

  --image-grid-height: 50vh;
  --image-grid-half-height: calc( var(--image-grid-height)/2 * 1px);
  --image-grid-half-height: calc( var(--image-grid-height)*2 * 1px);
}

body {
  margin: 0 !important;
  padding: 0 !important;
  font-family: Poppins, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background-color: #050505;

  background-color: var(--black);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.animated-text{
    display: flex;
    white-space: pre;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.animated-text.done .letter{
    opacity: 1;
    -webkit-animation: none !important;
            animation: none !important;
    transform: translateY(0);
}

.animated-text .letter{
    opacity: 0;
    -webkit-animation: show-letter 0.7s ease-out forwards;
            animation: show-letter 0.7s ease-out forwards;
}

@-webkit-keyframes show-letter{
    0%{
        opacity: 0;
        transform: translateY(50px);
    }
    30%{
        opacity: 1;
        transform: translateY(-20px);
    }
    100%{
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes show-letter{
    0%{
        opacity: 0;
        transform: translateY(50px);
    }
    30%{
        opacity: 1;
        transform: translateY(-20px);
    }
    100%{
        opacity: 1;
        transform: translateY(0);
    }
}
.text-carousel{
    position: relative;
}

.text-carousel .animated-text{
    position: relative;
    -webkit-filter: drop-shadow(0 0 1px var(--black));
            filter: drop-shadow(0 0 1px var(--black));
    z-index: 5;
}

.text-carousel .underline{
    position: absolute;
    bottom: 10%;
    left: 5%;

    width: 0px;
    height: 4px;
    background-color: var(--white);
    border-radius: 15px;

    -webkit-animation: underline 0.5s ease-out forwards;

            animation: underline 0.5s ease-out forwards;
    transform-origin: left;

    z-index: 1;
}

@-webkit-keyframes underline{
    to{
        width: 90%;
    }
}

@keyframes underline{
    to{
        width: 90%;
    }
}
.page{
    position: relative;
    
    padding: min(60px, 8vw) !important;
    min-height: 100%;
    min-width: 100%;

    box-sizing: border-box;
}

@media (max-width:500px){
    .page{
        padding-left: min(30px, 4vw) !important;
        padding-right: min(30px, 4vw) !important;
    }
}
.home{
    position: relative;

    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    color: var(--white);
}

.home #logo{
    fill: var(--white);
}

.home .card{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);

    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    
