@media (max-width: 500px){
    .home .page-arrow{
        display: none;
    }

    .home .hand-emoji{
        left: 53%;
        max-width: 75vw;
        max-height: 80vh;
        height: auto;
    }

    .home .card .animated-text{
        font-size: 6vw;
        margin: 5px;
