}
.bubbles{
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);

    width: 40vw;
    height: 40vw;
}

.bubbles *{
    border-radius: 50%;
    -webkit-filter: drop-shadow(0px 0px 10px rgba(32, 0, 15, 0.5));
            filter: drop-shadow(0px 0px 10px rgba(32, 0, 15, 0.5));
    z-index: 5;
}

.bubbles .b-1{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    width: 30vw;
    height: 30vw;

    -webkit-animation: bubble1 0.7s ease;

            animation: bubble1 0.7s ease;
}

.bubbles .b-2{
    position: absolute;
    bottom: 5%;
    left: 10%;

    width: 7vw;
    height: 7vw;

