    width: 5vw;
    height: 5vw;

    opacity: 0;
    -webkit-animation: bubble3 1.2s ease 1s forwards;
            animation: bubble3 1.2s ease 1s forwards;
}

.bubbles .b-4{
    position: absolute;
    right: 18%;
    top: 0%;

    width: 8vw;
    height: 8vw;
    
    z-index: 2;

    opacity: 0;
    -webkit-animation: bubble4 1.2s ease 0.8s forwards;
