.page-arrow{
    display: flex;
    align-items: center;
    justify-content: center;

    width: 50px;
    height: 50px;
    background-color: transparent;
    color: var(--white);
    font-size: 50px;

    cursor: pointer;
}

.page-arrow div{
    position: absolute;
    top: 65%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.page-arrow span{
    position: relative;
    left: 0;
    transform: translateX(-30%);

    width: -webkit-fit-content;

    width: -moz-fit-content;

    width: fit-content;
    margin-right: 200%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    font-size: 0.5em;
    font-weight: 600;
    line-height: 1em;
    text-align: right;
    text-transform: lowercase;

    opacity: 0;

    transition: all 0.3s ease-out;
}

.page-arrow.split span{
    transform: none;
    width: 100px;
    margin-right: 125px;
}

.page-arrow:hover span{
    opacity: 1;
    transform: translateX(-40%);
}

.page-arrow.split:hover span{
    opacity: 1;
    margin-right: 150px;
    transform: none;
}
.presentation{
    width: 45vw !important;
    height: min(650px, 72vh) !important;
}

.presentation .left-card{
    position: relative;
    height: 100%;
}

.presentation .me-img {
    position: absolute;
    left: 10vw;
    bottom: 5vw;
    height: min(600px, 65vh);

    z-index: 10;

    -webkit-animation: slide 1s ease-out;

            animation: slide 1s ease-out;
}

.presentation .name, .presentation .upper-name{
    position: absolute;
    left: 0;
    bottom: 0.65em;

    color: var(--sobre);
    font-size: max(4.25em, 8vw);
    line-height: max(0.8em, 6vw);
    letter-spacing: -0.3vw;
    font-weight: 700;
    text-transform: uppercase;

    z-index: 5;
}

.presentation .upper-name{
    z-index: 15;

    color: transparent;
    -webkit-text-stroke: 2px var(--sobre);
}

.presentation .additional-info{
    position: absolute;
    left: 0;
    bottom: 0px; 

    font-size: max(2.3em, 3.5vw);
    font-weight: 600;
    letter-spacing: min(-2px, -0.2vw);

    color: var(--white);

    z-index: 15;
}

@media (max-width: 800px){
    .presentation{
        width: min(400px, 95vw) !important;
        height: 75vh;
        margin-left: 1vw;
    }

    .presentation .me-img {
        bottom: max(45px, 7vw);
    }

    .presentation .name, .presentation .upper-name{
        bottom: 11%;

        font-size: min(17vw, 4.5em);
        line-height: max(0.8em, 6vw);
        letter-spacing: -0.3vw;
        font-weight: 700;
    }
}

@-webkit-keyframes slide{
    0%{
        transform: translateX(25vw);
        opacity: 0;
    }
    50%{
        opacity: 1;
    }
    100%{
        transform: translateX(0px);
        opacity: 1;
    }
}

@keyframes slide{
    0%{
        transform: translateX(25vw);
        opacity: 0;
    }
    50%{
        opacity: 1;
    }
    100%{
        transform: translateX(0px);
        opacity: 1;
    }
}
.certificades{
    display: grid;

    grid-template-rows: repeat(3, 1fr);
    grid-template-columns: repeat( auto-fit, 350px );
    grid-auto-flow: column;
    grid-column-gap: 100px;
    -webkit-column-gap: 100px;
            column-gap: 100px;

    width: -webkit-fit-content;

    width: -moz-fit-content;

    width: fit-content;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    max-height: 350px;
    padding-right: 50px;
}
.img-following-text{
    position: relative;

    display: flex;
    align-items: center;
    justify-content: flex-start;

    padding: 25px;
    width: 300px;

    border-top: 1px solid var(--white);
    border-bottom: 1px solid var(--white);
    color: var(--white);

    -webkit-user-select: none;

        -ms-user-select: none;

            user-select: none;
    cursor: pointer;
}

.img-following-text::after{
    content: "";
    position: absolute;
    top: 0;
    left: 0;

    width: 100%;
    height: 100%;
    background-color: violet;
    opacity: 0;

    z-index: 50;
}

.img-following-text span{
    text-transform: lowercase;
    text-align: left;
    font-size: 1.5em;
    font-weight: 600;

    color: var(--white);
    
    z-index: 10;
}

.img-following-text img{
    position: absolute;

    max-width: 50%;
    max-height: 90%;

    z-index: 5;

    opacity: 0;
    transition: all 0.5s ease-out;

    -webkit-user-select: none;

        -ms-user-select: none;

            user-select: none;
}

.img-following-text:hover img{
    opacity: 1;
}
.scrollable{
    position: relative;
    width: -webkit-fit-content;
    width: -moz-fit-content;
