    to{
        transform: translateY(0px);
        color: var(--white);
    }
}

@keyframes slide-letter{
    from{
        transform: translateY(250px);
        color: var(--black);
    }
    to{
        transform: translateY(0px);
        color: var(--white);
    }
}
.pages-selector{
    position: absolute;
    bottom: min(40px, 10vw);
    left: 50%;
    transform: translateX(-50%);

    display: flex;

    z-index: 1000;

    -webkit-user-select: none;

        -ms-user-select: none;

            user-select: none;
}

.pages-selector *:not(:last-child){
    margin-right: 20px;
}

.pages-selector label{
    position: relative;

    display: flex;
    align-items: center;
    justify-content: center;

    width: max(15px, 1.25vw);
    height: max(15px, 1.25vw);
    background-color: transparent;

    border: 3px solid var(--white);
    border-radius: 50%;

    cursor: pointer;
}

.pages-selector label input{
    position: absolute;
    opacity: 0;
    width: 0;
}

.pages-selector label .page-bubble{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    width: max(20px, 1.5vw);
    height: max(20px, 1.5vw);
    border-radius: 50%;
    background-color: transparent;

    transition: all 0.5s ease-out;
}

.pages-selector label input:checked+.page-bubble{
    background-color: var(--white);
}

.pages-selector label .page-name{
    position: absolute;
    bottom: 15px;
    left: 50%;
    transform: translate(-50%, 0);

    color: var(--white);
    font-size: 1.4em;
    text-transform: lowercase;

    opacity: 0;

    transition: all 0.5s ease-out;
}

.pages-selector label:hover .page-name{
    transform: translate(-50%, -30%);
    opacity: 1;
}
.glitch-text{
    position: absolute;
    top: 50%;
    right: 50%;
    transform: translate(50%, -50%);

    font-size: 3.5vw;

    -webkit-animation: shrink 0.6s ease-out 1.5s forwards;

            animation: shrink 0.6s ease-out 1.5s forwards;

    color: var(--black);
}

.glitch-text.quick{
    -webkit-animation-delay: 0ms;
            animation-delay: 0ms;
    -webkit-animation-duration: 0ms;
            animation-duration: 0ms;
}

.glitch-text.quick .repetitions{
    display: none;
}

.glitch-text .auxiliar-text{
    -webkit-text-stroke: 1px var(--black);
}

@media (max-width: 449px){
    .glitch-text{
        font-size: 4.5vw;
        -webkit-animation: small-shrink 0.6s ease-out 1.5s forwards;
                animation: small-shrink 0.6s ease-out 1.5s forwards;
    }

    .glitch-text span{
        line-height: 0.9em;
    }
}

@-webkit-keyframes shrink{
    to{
        top: calc(min(65px, 10vw) + 5px);
        right: calc(min(65px, 10vw) + 50px);
        transform: translate(0,0);

        font-size: 0.75em;
        font-weight: 600;
    }
}

@keyframes shrink{
    to{
        top: calc(min(65px, 10vw) + 5px);
        right: calc(min(65px, 10vw) + 50px);
        transform: translate(0,0);

        font-size: 0.75em;
        font-weight: 600;
    }
}

@-webkit-keyframes small-shrink{
    to{
        top: calc(min(65px, 10vw) + 5px);
        transform: translate(50%, 0%);

        font-size: 2.5vw;
        font-weight: 600;
    }
}

@keyframes small-shrink{
    to{
        top: calc(min(65px, 10vw) + 5px);
        transform: translate(50%, 0%);

        font-size: 2.5vw;
        font-weight: 600;
    }
}
.contact{
    height: calc(100vh - min(120px, 20vw) - 55px) !important;
    width: calc(100vw - min(120px, 20vw)) !important;

    padding-top: 55px;
    max-height: 700px;
}

.contact .animated-text{
    color: var(--white);

    font-size: 5.5vw;
    line-height: 5.5vw;
    font-weight: 600;
    letter-spacing: -0.2vw;
}

.contact .headline{
    height: 11vw;
}

.contact .left-container{
    display: grid;
    grid-template-rows: repeat(2, 1fr);
    align-items: center;

    height: 100%;
}

.contact .left-container .top{
    padding: 5vh 0;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.contact .left-container .bottom{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-column-gap: 50px;
    -webkit-column-gap: 50px;
            column-gap: 50px;
    grid-row-gap: 25px;
    row-gap: 25px;

    width: -webkit-fit-content;

    width: -moz-fit-content;

    width: fit-content;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
}

.contact .left-container .social{
    display: flex;
    margin-top: 5px;
}

.contact .left-container .social .social-icon{
    display: flex;
    align-items: center;
    justify-content: center;

    color: var(--white);
    font-size: 1.4em;
    text-decoration: none;
}

.contact .left-container .social-icon *{
    transition: all 0.3s ease-out;
}

.contact .left-container .social-icon:hover *{
    color: var(--contato);
}

.contact .resume-btn{
    position: relative;

    font-family: inherit;
    border: 1px solid var(--white);
    border-radius: 5px;
    color: var(--white);

    background-color: transparent;
    padding: 10px 20px;
    text-transform: uppercase;
    letter-spacing: 1px;

    margin-top: 5px;

    transition: all 0.5s ease-out;

    cursor: pointer;
}

.contact .resume-btn:hover{
    color: var(--black);
}

.contact .resume-btn::after{
    content: "";
    position: absolute;
    top: 0;
    left: 0;

    width: 0%;
    height: 100%;
    background-color: var(--white);

    transition: all 0.5s ease-out;

    z-index: -1;
}

.contact .resume-btn:hover::after{
    width: 100%;
}

.contact .resume-btn a{
    font-family: inherit;
    text-decoration: none;
    color: inherit;
}

.contact .bottom .simple-info{
    opacity: 0;
    -webkit-animation: show-bottom 0.5s ease-out forwards;
            animation: show-bottom 0.5s ease-out forwards;
}

.contact .bottom .email{
    -webkit-animation-delay: 0.8s;
            animation-delay: 0.8s;
}

.contact .bottom .phone{
    -webkit-animation-delay: 0.9s;
            animation-delay: 0.9s;
}

.contact .bottom .social-container{
    -webkit-animation-delay: 1s;
            animation-delay: 1s;
}

.contact .bottom .resume{
    -webkit-animation-delay: 1.1s;
            animation-delay: 1.1s;
}

.contact .lottie{
    position: absolute;
    top: 5%;
    right: 3%;
    width: 70vh;
    height: 70vh;

    max-width: 1000px;
    max-height: 1000px;
    /* background-color: blue; */
}

@media (max-width: 500px){
    .contact{
